<template>
  <div class="main-container">
    <component
      :is="currentStep"
      :data="data"
      class="step"
      @next="nextStep"
      @onInputFocus="onInputFocus"
      @onInputChange="onInputChange"
    />
    <div v-if="focusedInput" class="keyboard-container">
      <SimpleKeyboard
        @onChange="onChange"
        :input="data[focusedInput]"
        :focusedInput="focusedInput"
      />
    </div>
  </div>
</template>

<script>
import HelloStep from "@/components/HelloStep.vue";
import PresentStep from "@/components/PresentStep.vue";
import PersonalDataStep from "@/components/PersonalDataStep.vue";
import CompanyDataStep from "@/components/CompanyDataStep.vue";
import RecommendDataStep from "@/components/RecommendDataStep.vue";
import ThanksStep from "@/components/ThanksStep.vue";

import SimpleKeyboard from "@/components/SimpleKeyboard.vue";

const steps = [
  HelloStep,
  PresentStep,
  PersonalDataStep,
  CompanyDataStep,
  RecommendDataStep,
  ThanksStep,
];

export default {
  components: {
    HelloStep,
    PresentStep,
    PersonalDataStep,
    CompanyDataStep,
    RecommendDataStep,
    SimpleKeyboard,
    ThanksStep,
  },

  data() {
    return {
      step: 0,
      data: {
        name: "",
        email: "",
        phone: "",
        company: "",
        recname: "",
        recphone: "",
      },
      focusedInput: "",
    };
  },

  computed: {
    currentStep() {
      return steps[this.step];
    },
  },

  methods: {
    nextStep() {
      this.step++;
      this.focusedInput = "";
    },

    onChange(input) {
      this.data[this.focusedInput] = input;
    },

    onInputChange(input) {
      this.data[input.target.id] = input.target.value;
    },

    onInputFocus(input) {
      this.focusedInput = input.target.id;
    },
  },
};
</script>

<style scoped>
.main-container {
  max-width: 1920px;
  margin: 0 auto;
  padding: 70px 50px;
}

.step {
  margin-left: auto;
}

.keyboard-container {
  position: absolute;
  top: 40%;
  right: 700px;
  width: 900px;
}
</style>
