<template>
  <div class="btn-container">
    <button type="button" class="btn" :class="getClass()" @click="onClick">
      <slot />
    </button>
  </div>
</template>

<script>
export default {
  props: {
    btnType: {
      type: String,
      default: "main",
    },
    action: {
      type: String,
      default: "",
    },
  },

  methods: {
    getClass() {
      return this.btnType === "main" ? "btn_main" : "btn_minor";
    },

    onClick() {
      this.$emit(this.action);
    },
  },
};
</script>

<style scoped>
.btn-container {
  width: 100%;
  text-align: center;
}

.btn {
  display: inline-block;
  border: 0;
  cursor: pointer;
}

.btn_main {
  background: var(--second-color);
  color: var(--second-text-color);
  font-size: var(--main-btn-font-size);
  border-radius: var(--second-border-radius);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 10px 30px 12px;
}
</style>
